
.c-author-header {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 260px;
  padding-top: 40px;
  background-image: url('../common/images/img/banner-1.png');
  background-size: cover;
}
.c-author-header .c-author-header-wrap {
  width: 960px;
  margin: 0 auto;
}
.c-author-header_left {
  position: relative;
  float: left;
  width: 180px;
  height: 180px;
  margin-right: 20px;
  background-color: #3c3c3c;
  border-radius: 50%;
}
.c-author-header_left .img {
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 50%;
}
.c-author-header_left .icon {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #323232;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/author-6.png');
  cursor: pointer;
}
.c-author-header_left .icon:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/author-6_on.png');
}
.c-author-header_right {
  float: left;
  width: 760px;
}
.c-author-header_right .name {
  height: 30px;
  overflow: hidden;
  margin-bottom: 15px;
}
.c-author-header_right .name .name-text {
  float: left;
  margin-right: 15px;
  color: #dcdcdc;
  font-size: 30px;
  font-weight: bolder;
}
.c-author-header_right .name .name-time {
  float: left;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  color: #dcdcdc;
}
.c-author-header_right .btn {
  height: 40px;
  margin-bottom: 15px;
}
.c-author-header_right .btn .btn-item {
  float: left;
  height: 40px;
  line-height: 40px;
  margin-right: 10px;
  background-color: #373737;
  border-radius: 4px;
  text-align: center;
  color: #dcdcdc;
  font-size: 14px;
  cursor: pointer;
}
.c-author-header_right .btn .btn-item a {
  display: inline-block;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: #dcdcdc;
  font-size: 14px;
}
.c-author-header_right .btn .btn-item .cpm_sub_more {
  width: 150px;
}
.c-author-header_right .btn .btn-item:hover .cpm_sub_more {
  display: block;
}
.c-author-header_right .btn .btn-item:last-child {
  margin-right: 0;
}
.c-author-header_right .btn .btn-mark {
  width: 600px;
}
.c-author-header_right .btn .btn-share {
  width: 100px;
  position: relative;
  cursor: pointer;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/author-5.png');
}
.c-author-header_right .btn .btn-share:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/author-5_on.png');
}
.c-author-header_right .btn .btn-more {
  width: 40px;
  position: relative;
  cursor: pointer;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/author-4.png');
}
.c-author-header_right .btn .btn-more:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/author-4_on.png');
}
.c-author-header_right .text {
  line-height: 1.5rem;
  color: #dcdcdc;
  font-size: 14px;
}



