
.c-comment-info .c-comment-info-wrap {
  margin-bottom: 30px;
  background-color: #4b4b4b;
}
.c-comment-info-list {
  padding: 20px;
  border-bottom: 1px solid #191919;
}
.c-comment-info__top {
  height: 20px;
  overflow: hidden;
  margin-bottom: 10px;
}
.c-comment-info__top .top__img {
  float: left;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  overflow: hidden;
  border-radius: 50%;
  background-color: #373737;
}
.c-comment-info__top .top__name {
  float: left;
  height: 20px;
  line-height: 20px;
  font-size: 12px;
  color: #828282;
}
.c-comment-info__top .top__time {
  float: right;
  height: 20px;
  line-height: 20px;
  font-size: 12px;
  color: #828282;
}
.c-comment-info__content {
  padding-left: 30px;
  line-height: 1.5rem;
  font-size: 14px;
  color: #a0a0a0;
}
.c-comment-info__content strong {
  color: #00beff;
}



