
.c-article-header {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 320px;
  padding-top: 40px;
  background-image: url('../common/images/img/banner-1.png');
  background-size: cover;
}
.c-article-header .c-article-header-wrap {
  width: 960px;
  margin: 0 auto;
}
.c-article-header_left {
  position: relative;
  float: left;
  width: 150px;
  height: 240px;
  margin-right: 20px;
  background-color: #373737;
}
.c-article-header_left .left-img {
  width: 100%;
  height: 240px;
  overflow: hidden;
}
.c-article-header_left .left-img img {
  width: 100%;
  height: 100%;
}
.c-article-header_left .left-tag {
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
}
.c-article-header_left .left-tag.z-man {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/article-4.png');
}
.c-article-header_left .left-tag.z-wen {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/article-3.png');
}
.c-article-header_right {
  position: relative;
  float: left;
  width: 790px;
  height: 240px;
}
.c-article-header_right .name {
  overflow: hidden;
  margin-bottom: 15px;
}
.c-article-header_right .name .name-text {
  float: left;
  color: #dcdcdc;
  font-size: 30px;
  font-weight: bolder;
  margin-right: 10px;
}
.c-article-header_right .name .name-tag {
  float: left;
  width: 52px;
  height: 30px;
  line-height: 30px;
  border-radius: 15px;
  font-size: 14px;
  color: #b4a000;
  background-color: #373737;
  text-align: center;
}
.c-article-header_right .btn {
  height: 40px;
  margin-bottom: 15px;
}
.c-article-header_right .btn .btn-item {
  float: left;
  height: 40px;
  line-height: 40px;
  margin-right: 10px;
  background-color: #373737;
  border-radius: 4px;
  text-align: center;
  color: #dcdcdc;
  font-size: 14px;
  cursor: pointer;
}
.c-article-header_right .btn .btn-item a {
  display: inline-block;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: #dcdcdc;
  font-size: 14px;
}
.c-article-header_right .btn .btn-item .cpm_sub_more {
  width: 150px;
}
.c-article-header_right .btn .btn-item:hover .cpm_sub_more {
  display: block;
}
.c-article-header_right .btn .btn-item:last-child {
  margin-right: 0;
}
.c-article-header_right .btn .btn-mark {
  width: 600px;
}
.c-article-header_right .btn .btn-mark span {
  display: block;
  width: 100%;
  height: 40px;
  line-height: 40px;
}
.c-article-header_right .btn .btn-share {
  width: 100px;
  position: relative;
  cursor: pointer;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/author-5.png');
}
.c-article-header_right .btn .btn-share:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/author-5_on.png');
}
.c-article-header_right .btn .btn-more {
  width: 40px;
  position: relative;
  cursor: pointer;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/author-4.png');
}
.c-article-header_right .btn .btn-more:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/author-4_on.png');
}
.c-article-header_right .text .text-intro {
  margin-bottom: 10px;
  line-height: 1.5rem;
  color: #dcdcdc;
  font-size: 14px;
}
.c-article-header_right .text .text-tag p {
  display: inline-block;
  margin-right: 10px;
  margin-bottom: 10px;
  color: #dcdcdc;
  font-size: 14px;
}
.c-article-header_right .text .text-number {
  overflow: hidden;
}
.c-article-header_right .text .text-number .text-number-item {
  display: inline-block;
  height: 12px;
}
.c-article-header_right .text .text-number .text-number-item span {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 4px;
  vertical-align: middle;
}
.c-article-header_right .text .text-number .text-number-item strong {
  display: inline-block;
  color: #dcdcdc;
  font-size: 12px;
  vertical-align: middle;
}
.c-article-header_right .text .text-number .text-zan {
  margin-right: 15px;
}
.c-article-header_right .text .text-number .text-zan span {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/article-5.png');
}
.c-article-header_right .text .text-number .text-comment span {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/article-6.png');
}
.c-article-header_right .text .text-author {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 20px;
  overflow: hidden;
}
.c-article-header_right .text .text-author__img {
  float: left;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  overflow: hidden;
  background-color: #3c3c3c;
  border-radius: 50%;
}
.c-article-header_right .text .text-author__name {
  float: left;
  height: 20px;
  line-height: 20px;
  color: #dcdcdc;
  font-size: 12px;
}



