
.c-page-fenye {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 100%;
  height: 30px;
  overflow: hidden;
  text-align: center;
}
.c-page-fenye .item {
  width: 30px;
  height: 30px;
  line-height: 30px;
  margin-right: 10px;
  vertical-align: middle;
  border-radius: 4px;
  display: inline-block;
  background-color: #4b4b4b;
  font-size: 14px;
  color: #a0a0a0;
  cursor: pointer;
  text-align: center;
}
.c-page-fenye .item.z-on {
  background-color: #323232;
  color: #dcdcdc;
}
.c-page-fenye .item:hover {
  color: #dcdcdc;
}
.c-page-fenye .item.z-prev {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/prev.png');
}
.c-page-fenye .item.z-prev:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/prev_on.png');
}
.c-page-fenye .item.z-next {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/next.png');
}
.c-page-fenye .item.z-next:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/next_on.png');
}
.c-page-fenye .text {
  margin-left: 10px;
  vertical-align: middle;
  font-size: 14px;
  color: #a0a0a0;
  display: none;
}
.c-page-fenye .text span {
  margin-left: 5px;
  color: #e61e5a;
}
.c-page-fenye .input {
  width: 60px;
  height: 40px;
  margin: 0 10px;
  vertical-align: middle;
  display: none;
  background-color: #4b4b4b;
}
.c-page-fenye .input input {
  text-align: center;
}
.c-page-fenye .btn {
  vertical-align: middle;
  display: none;
}
.c-page-fenye.z-small {
  text-align: left;
  color: #a0a0a0;
}
.c-page-fenye.z-small .item {
  border-radius: 50%;
  background: none;
}
.c-page-fenye.z-small .item:hover {
  background-color: #3c3c3c;
  color: #dcdcdc;
}
.c-page-fenye.z-small .item.z-on {
  background-color: #000;
  color: #a0a0a0;
}
.c-page-fenye.z-small .item.z-prev {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/prev.png');
}
.c-page-fenye.z-small .item.z-next {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/next.png');
}
.c-page-fenye.z-width {
  text-align: left;
  color: #a0a0a0;
}
.c-page-fenye.z-width .text,
.c-page-fenye.z-width .input,
.c-page-fenye.z-width .btn {
  display: inline-block;
}



