
.c-header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
  width: 100%;
  min-width: 960px;
  height: 60px;
  background-color: #323232;
}
.c-header-content {
  width: 960px;
  height: 100%;
  margin: 0 auto;
}
.c-header-content .logo {
  float: left;
  width: 84px;
  height: 50px;
  margin-top: 5px;
  margin-right: 20px;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/logo.png');
  cursor: pointer;
}
.c-header-content .logo a {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.c-header-content .menu {
  float: left;
  margin-top: 10px;
  margin-right: 30px;
  overflow-y: hidden;
}
.c-header-content .menu .item {
  float: left;
  height: 40px;
  line-height: 40px;
  margin-right: 20px;
  font-size: 14px;
  color: #a0a0a0;
  text-decoration: none;
}
.c-header-content .menu .item:hover {
  color: #b4b4b4;
}
.c-header-content .menu .item.z-on {
  color: #b4b4b4;
}
.c-header-content .menu .item :last-child {
  margin-right: 0;
}
.c-header-content .search {
  float: left;
  width: 270px;
  height: 40px;
  overflow: hidden;
  padding-left: 20px;
  margin-top: 10px;
  border-radius: 4px;
  background-color: #414141;
}
.c-header-content .search input {
  display: block;
  float: left;
  width: 200px;
  height: 40px;
  line-height: 40px;
  color: #a0a0a0;
  font-size: 14px;
}
.c-header-content .search a {
  display: block;
  float: left;
  width: 50px;
  height: 40px;
  padding-top: 10px;
  background-color: #373737;
  text-align: center;
}
.c-header-content .search a span {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/search.png');
}
.c-header-content .search a span:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/search_on.png');
}
.c-header-content .search a:hover {
  background-color: #3c3c3c;
}
.c-header-content .user {
  float: right;
  height: 60px;
  margin-right: 20px;
  text-align: center;
}
.c-header-content .user .btn {
  padding: 15px 0;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  color: #a0a0a0;
  font-weight: bolder;
}
.c-header-content .user .btn a {
  text-decoration: none;
  color: #dcdcdc;
}
.c-header-content .user .btn a:hover {
  color: #b4b4b4;
}
.c-header-content .user .btn span {
  color: #a0a0a0;
  font-size: 14px;
  font-weight: bolder;
}
.c-header-content .user .member {
  position: relative;
  height: 60px;
}
.c-header-content .user .member .member-show-item {
  width: 30px;
  height: 60px;
  padding: 15px 0;
  float: left;
}
.c-header-content .user .member .member-show-notice {
  position: relative;
  margin-right: 20px;
}
.c-header-content .user .member .member-show-notice .member-show-notice_img {
  width: 100%;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/notice.png');
  cursor: pointer;
  border-radius: 50%;
  background-color: #4b4b4b;
}
.c-header-content .user .member .member-show-notice .member-show-notice_img:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/notice_on.png');
}
.c-header-content .user .member .member-show-notice .member-show-notice_number {
  position: absolute;
  top: 15px;
  right: 0;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #e61450;
}
.c-header-content .user .member .member-show-notice .member-notice {
  display: none;
  position: absolute;
  top: 60px;
  left: 0;
  width: 180px;
  z-index: 11;
  overflow: hidden;
