
.c-comment-info .c-comment-info-wrap {
  margin-bottom: 30px;
}
.c-comment-info-list {
  padding: 20px 30px 20px 20px;
  border-bottom: 1px solid #191919;
  background-color: #4b4b4b;
}
.c-comment-info-list.z-unread {
  background-color: #414141;
}
.c-comment-info__top {
  height: 20px;
  overflow: hidden;
  margin-bottom: 10px;
}
.c-comment-info__top .top__img {
  float: left;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  border-radius: 50%;
  background-color: #373737;
}
.c-comment-info__top .top__name {
  float: left;
  height: 20px;
  line-height: 20px;
  font-size: 12px;
  color: #828282;
}
.c-comment-info__top .top__detail {
  float: right;
  height: 20px;
  line-height: 20px;
}
.c-comment-info__top .top__detail a {
  font-size: 12px;
  color: #828282;
}
.c-comment-info__comment {
  padding-left: 30px;
  margin-bottom: 15px;
  color: #a0a0a0;
  font-size: 14px;
}
.c-comment-info__comment .comment__top {
  margin-bottom: 15px;
  color: #828282;
}
.c-comment-info__comment .comment__top strong {
  color: #b4a000;
}
.c-comment-info__comment .comment__top span {
  color: #00beff;
}
.c-comment-info__comment .comment__body {
  line-height: 1.5rem;
  margin-bottom: 15px;
}
.c-comment-info__comment .comment__bottom {
  line-height: 1.5rem;
  padding: 20px;
  border-radius: 4px;
  color: #828282;
  font-size: 14px;
  background-color: #2d2d2d;
}
.c-comment-info__form {
  padding-left: 30px;
  overflow: hidden;
}
.c-comment-info__form .form__top {
  height: 12px;
  margin-bottom: 15px;
  font-size: 12px;
  color: #828282;
}
.c-comment-info__form .form__top__jubao {
  float: left;
  cursor: pointer;
  font-size: 12px;
  color: #828282;
}
.c-comment-info__form .form__top__jubao:hover {
  color: #dcdcdc;
}
.c-comment-info__form .form__top__record {
  float: right;
  cursor: pointer;
  font-size: 12px;
  color: #a0a0a0;
}
.c-comment-info__form .form__top__record:hover {
  color: #dcdcdc;
}
.c-comment-info__form .form__body {
  width: 100%;
  overflow: hidden;
}
.c-comment-info__form .form__body__write {
  float: left;
  width: 520px;
  height: 40px;
  background-color: #3c3c3c;
}
.c-comment-info__form .form__body__write input {
  width: 520px;
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  color: #5a5a5a;
  font-size: 14px;
}
.c-comment-info__form .form_body__commit {
  float: right;
  width: 70px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border-radius: 4px;
  color: #a0a0a0;
  font-size: 14px;
  background-color: #373737;
  cursor: pointer;
}
.c-comment-info__form .form_body__commit:hover {
  color: #dcdcdc;
}



