
.c-artice-hot {
  width: 100%;
  height: 500px;
  padding-top: 40px;
  background-color: #3c3c3c;
}
.c-artice-hot .c-artice-hot-wrap {
  height: 460px;
  width: 960px;
  overflow: hidden;
  margin: 0 auto;
}
.c-artice-hot .c-artice-hot-wrap .hot-top {
  width: 100%;
  height: 50px;
  padding-top: 30px;
  margin-bottom: 20px;
  overflow: hidden;
  background-image: url('../common/images/img/bg-1.png');
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right {
  float: right;
  height: 20px;
  overflow: hidden;
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item {
  float: left;
  height: 20px;
  line-height: 20px;
  color: #a0a0a0;
  font-size: 14px;
  vertical-align: middle;
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item-number {
  margin-right: 8px;
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item-btn {
  position: relative;
  width: 20px;
  height: 20px;
  background-color: #4b4b4b;
  border-radius: 4px;
  cursor: pointer;
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item-btn span {
  position: absolute;
  display: block;
  width: 20px;
  height: 20px;
  transition: opacity 0.25s;
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item-left {
  margin-right: 5px;
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item-left span:nth-child(1) {
  opacity: 1;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/left-1.png');
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item-left span:nth-child(2) {
  opacity: 0;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/left-1_on.png');
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item-left:hover span:nth-child(1) {
  opacity: 0;
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item-left:hover span:nth-child(2) {
  opacity: 1;
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item-right span:nth-child(1) {
  opacity: 1;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/right-1.png');
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item-right span:nth-child(2) {
  opacity: 0;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/right-1_on.png');
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item-right:hover span:nth-child(1) {
  opacity: 0;
}
.c-artice-hot .c-artice-hot-wrap .hot-top .hot-top-right .item-right:hover span:nth-child(2) {
  opacity: 1;
}
.c-artice-hot .c-artice-hot-wrap .hot-list {
  width: 100%;
  height: 350px;
  padding-left: 10px;
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list-item {
  position: relative;
  float: left;
  width: 140px;
  height: 350px;
  padding: 10px;
  margin-right: 10px;
  transition: all 0.25s;
  background-color: #4b4b4b;
  border-radius: 4px;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list-item a {
  display: block;
  width: 100%;
  height: 100%;
  text-decoration: none;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list-item:hover {
  -webkit-transform: translate(0, -2px);
          transform: translate(0, -2px);
  box-shadow: 0 0 30px #323232;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list__hd {
  position: relative;
  width: 120px;
  height: 160px;
  margin-bottom: 12px;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list__hd .hot-list__hd-img {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #373737;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list__hd .hot-list__hd-img img {
  max-width: 100%;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list__hd .hot-list__hd-text {
  position: absolute;
  right: 8px;
  bottom: 8px;
  width: 40px;
  height: 20px;
  line-height: 18px;
  text-align: center;
  border: 1px solid #232323;
  border-radius: 10px;
  color: #dcdcdc;
  font-size: 12px;
  background-color: #414141;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list__title {
  line-height: 1.5rem;
  margin-bottom: 10px;
  font-size: 18px;
  color: #dcdcdc;
  font-weight: bolder;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list__title:hover {
  color: #b4b4b4;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list__tag a {
  float: left;
  margin-right: 8px;
  margin-bottom: 8px;
  font-size: 12px;
  color: #828282;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list__tag a:hover {
  color: #dcdcdc;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list__ft {
  position: absolute;
  left: 10px;
  bottom: 10px;
  height: 20px;
  overflow: hidden;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list__ft-img {
  float: left;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  overflow: hidden;
  background-color: #373737;
  border-radius: 50%;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list__ft-name {
  width: 90px;
  height: 20px;
  line-height: 20px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 12px;
  color: #828282;
}
.c-artice-hot .c-artice-hot-wrap .hot-list .hot-list__ft-name:hover {
  color: #dcdcdc;
}



