
.c-author-hot {
  width: 100%;
}
.c-author-hot .list-author__title {
  width: 100%;
  height: 60px;
  padding-top: 40px;
  margin-bottom: 20px;
}
.c-author-hot .list-author__title .text {
  float: left;
  font-size: 20px;
  color: #a0a0a0;
  font-weight: bolder;
}
.c-author-hot .list-author__title .list-author__page {
  float: right;
  height: 20px;
  overflow: hidden;
}
.c-author-hot .list-author__title .item {
  float: left;
  height: 20px;
  line-height: 20px;
  color: #a0a0a0;
  font-size: 14px;
  vertical-align: middle;
}
.c-author-hot .list-author__title .item-number {
  margin-right: 8px;
}
.c-author-hot .list-author__title .item-btn {
  position: relative;
  width: 20px;
  height: 20px;
  background-color: #4b4b4b;
  border-radius: 4px;
  cursor: pointer;
}
.c-author-hot .list-author__title .item-btn span {
  position: absolute;
  display: block;
  width: 20px;
  height: 20px;
  transition: opacity 0.25s;
}
.c-author-hot .list-author__title .item-left {
  margin-right: 5px;
}
.c-author-hot .list-author__title .item-left span:nth-child(1) {
  opacity: 1;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/left-1.png');
}
.c-author-hot .list-author__title .item-left span:nth-child(2) {
  opacity: 0;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/left-1_on.png');
}
.c-author-hot .list-author__title .item-left:hover span:nth-child(1) {
  opacity: 0;
}
.c-author-hot .list-author__title .item-left:hover span:nth-child(2) {
  opacity: 1;
}
.c-author-hot .list-author__title .item-right span:nth-child(1) {
  opacity: 1;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/right-1.png');
}
.c-author-hot .list-author__title .item-right span:nth-child(2) {
  opacity: 0;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/right-1_on.png');
}
.c-author-hot .list-author__title .item-right:hover span:nth-child(1) {
  opacity: 0;
}
.c-author-hot .list-author__title .item-right:hover span:nth-child(2) {
  opacity: 1;
}
.c-author-hot.z-active .list-author__title {
  background-image: url('../common/images/img/bg-6.png');
}
.c-author-hot .list-author__body {
  width: 100%;
  height: 350px;
  background-color: #4b4b4b;
  border-radius: 4px;
}
.c-author-hot .list-author__body .list-author__item {
  display: table;
  width: 260px;
  height: 70px;
  padding: 0 20px;
  transition: box-shadow 0.25s;
  border-bottom: 1px solid #191919;
}
.c-author-hot .list-author__body .list-author__item:hover {
  -webkit-transform: translate(0, -1px);
          transform: translate(0, -1px);
  box-shadow: 0 0 30px #323232;
  border-bottom: 1px solid #4b4b4b;
}
.c-author-hot .list-author__body .list-author__item a {
  text-decoration: none;
}
.c-author-hot .list-author__body .list-author__item:first-child {
  border-radius: 4px 4px 0 0;
}
.c-author-hot .list-author__body .list-author__item:last-child {
  border-radius: 0 0 4px 4px;
  border: none;
}
.c-author-hot .list-author__body .list-author__item .list-author__img {
  display: table-cell;
  vertical-align: middle;
  width: 30px;
}
.c-author-hot .list-author__body .list-author__item .list-author__avatar {
  width: 30px;
  height: 30px;
  overflow: hidden;
  border-radius: 50%;
  background-color: #373737;
}
.c-author-hot .list-author__body .list-author__item .list-author__content {
  display: table-cell;
  vertical-align: middle;
  width: 180px;
  padding-left: 10px;
}
.c-author-hot .list-author__body .list-author__item .list-author__name {
  margin-bottom: 6px;
  color: #dcdcdc;
  font-size: 12px;
}
.c-author-hot .list-author__body .list-author__item .list-author__name:hover {
  color: #b4b4b4;
}
.c-author-hot .list-author__body .list-author__item .list-author__text {
  overflow: hidden;
  font-size: 12px;
  color: #828282;
}
.c-author-hot .list-author__body .list-author__item .list-author__work {
  float: left;
  margin-right: 10px;
}
.c-author-hot .list-author__body .list-author__item .list-author__fans {
  float: left;
}
.c-author-hot .list-author__body .list-author__item .list-author__btn {
  display: none;
  float: right;
  color: #a0a0a0;
  cursor: pointer;
}
.c-author-hot .list-author__body .list-author__item .list-author__btn.z-active {
  color: #828282;
}
.c-author-hot .list-author__body .list-author__item:hover .list-author__btn {
  display: block;
}



