
.c-artice-comment {
  width: 100%;
  height: 100%;
}
.c-artice-comment .c-artice-comment-wrap {
  margin-bottom: 30px;
  width: 100%;
  height: 100%;
  background-color: #4b4b4b;
  border-radius: 4px;
}
.c-artice-comment-list {
  border-bottom: 1px solid #191919;
}
.c-artice-comment-list .comment__content {
  padding: 20px 30px 20px 20px;
}
.c-artice-comment-list .comment__content .content__top {
  height: 20px;
  margin-bottom: 10px;
}
.c-artice-comment-list .comment__content .content__top .top-img {
  float: left;
  width: 20px;
  height: 20px;
  overflow: hidden;
  margin-right: 10px;
  border-radius: 50%;
  background-color: #373737;
}
.c-artice-comment-list .comment__content .content__top .top-name {
  float: left;
  height: 20px;
  line-height: 20px;
  color: #828282;
  font-size: 16px;
}
.c-artice-comment-list .comment__content .content__body {
  padding-left: 30px;
  line-height: 1.5rem;
  margin-bottom: 15px;
  color: #a0a0a0;
  font-size: 14px;
}
.c-artice-comment-list .comment__content .content__bottom {
  padding-left: 30px;
  height: 12px;
  overflow: hidden;
  color: #828282;
  font-size: 12px;
}
.c-artice-comment-list .comment__content .content__bottom .bottom-jubao {
  float: left;
  height: 12px;
  line-height: 12px;
  cursor: pointer;
}
.c-artice-comment-list .comment__content .content__bottom .bottom-jubao:hover {
  color: #dcdcdc;
}
.c-artice-comment-list .comment__content .content__bottom .bottom-time {
  float: right;
  height: 12px;
  line-height: 12px;
  margin-right: 15px;
}
.c-artice-comment-list .comment__content .content__bottom .bottom-fabulous {
  float: right;
  height: 12px;
  margin-right: 12px;
  cursor: pointer;
}
.c-artice-comment-list .comment__content .content__bottom .bottom-fabulous span {
  float: left;
  display: block;
  width: 12px;
  height: 12px;
  margin-right: 4px;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/fabulous.png');
}
.c-artice-comment-list .comment__content .content__bottom .bottom-fabulous span:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/fabulous_on.png');
}
.c-artice-comment-list .comment__content .content__bottom .bottom-fabulous strong {
  float: right;
  height: 12px;
}
.c-artice-comment-list .comment__content .content__bottom .bottom-record {
  float: right;
  height: 12px;
  line-height: 12px;
  color: #a0a0a0;
  text-align: center;
  cursor: pointer;
}
.c-artice-comment-list .comment__content .content__bottom .bottom-record:hover {
  color: #dcdcdc;
}
.c-artice-comment-list .comment__record .record__item-content {
  padding: 10px 30px 10px 50px;
}
.c-artice-comment-list .comment__record .record__item-content .item-text p {
  line-height: 1.5rem;
  margin-bottom: 15px;
  color: #a0a0a0;
  font-size: 14px;
}
.c-artice-comment-list .comment__record .record__item-content .item-text strong {
  color: #b4a000;
  font-size: 12px;
}
.c-artice-comment-list .comment__record .record__item-content .item-text em {
  font-style: normal;
}
.c-artice-comment-list .comment__record .record__item-content .item-bottom {
  overflow: hidden;
  color: #828282;
  font-size: 12px;
}
.c-artice-comment-list .comment__record .record__item-content .item-bottom-jubao {
  float: left;
  cursor: pointer;
}
.c-artice-comment-list .comment__record .record__item-content .item-bottom-jubao:hover {
  color: #dcdcdc;
}
.c-artice-comment-list .comment__record .record__item-content .item-bottom-time {
  float: right;
  margin-right: 15px;
}
.c-artice-comment-list .comment__record .record__item-content .item-bottom-record {
  float: right;
  cursor: pointer;
  color: #a0a0a0;
}
.c-artice-comment-list .comment__record .record__item-content .item-bottom-record:hover {
  color: #dcdcdc;
}
.c-artice-comment-list .comment__record .record__page {
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 14px;
  color: #828282;
  border-top: 1px solid #191919;
}
.c-artice-comment-list .comment__record .record__page span {
  color: #a0a0a0;
}
.c-artice-comment-list .comment__record .record__page strong {
  color: #00beff;
  cursor: pointer;
}
.c-artice-comment-list .content__reply {
  height: 80px;
  padding: 20px 30px 20px 50px;
  overflow: hidden;
}
.c-artice-comment-list .content__reply .content__reply-form {
  float: left;
  height: 40px;
  width: 520px;
  margin-right: 10px;
  padding: 0 20px;
  background-color: #3c3c3c;
  border-radius: 4px;
  color: #828282;
  font-size: 14px;
}
.c-artice-comment-list .content__reply .content__reply-form input {
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  color: #828282;
}
.c-artice-comment-list .content__reply .content__reply-btn {
  float: left;
  width: 70px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #a0a0a0;
  font-size: 14px;
  background-color: #373737;
  border-radius: 4px;
  cursor: pointer;
  color: #dcdcdc;
}



