
.c-artice-list {
  width: 680px;
  height: 100%;
}
.c-artice-list .c-artice-list-wrap {
  width: 100%;
  height: 100%;
  min-height: 350px;
  background-color: #4b4b4b;
  border-radius: 4px;
}
.c-artice-list .list-more {
  width: 100%;
  text-align: center;
  background-color: #4b4b4b;
  border-radius: 0 0 4px 4px;
}
.c-artice-list .list-more .btn-more-before {
  height: 60px;
  line-height: 60px;
  color: #dcdcdc;
  font-size: 14px;
  cursor: pointer;
}
.c-artice-list .list-more .btn-more-loading {
  height: 60px;
  line-height: 60px;
}
.c-artice-list .list-more .btn-more-loading em {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  vertical-align: middle;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/load.png');
  -webkit-animation: loading 1.5s linear infinite;
          animation: loading 1.5s linear infinite;
}
.c-artice-list .list-more .btn-more-loading span {
  display: inline-block;
  color: #dcdcdc;
  font-weight: bolder;
  font-size: 14px;
  vertical-align: middle;
}
.c-artice-list .list-more .btn-more-end {
  height: 60px;
  line-height: 60px;
  color: #a0a0a0;
  font-size: 14px;
}
.c-artice-list .list-pages {
  margin-top: 30px;
}
.c-artice-item {
  width: 100%;
  height: 180px;
  padding: 10px 20px 10px 10px;
  background-color: #4b4b4b;
  border-bottom: 1px solid #191919;
  transition: box-shadow 0.25s, -webkit-transform 0.25s;
  transition: box-shadow 0.25s, transform 0.25s;
  transition: box-shadow 0.25s, transform 0.25s, -webkit-transform 0.25s;
}
.c-artice-item .c-artice-item-wrap,
.c-artice-item .c-artice-item-wrap-link {
  display: block;
  width: 100%;
  height: 160px;
}
.c-artice-item .item-left,
.c-artice-item .item-right {
  float: left;
  height: 160px;
}
.c-artice-item .item-left {
  position: relative;
  width: 120px;
  height: 160px;
  overflow: hidden;
  margin-right: 20px;
}
.c-artice-item .item-left .item-left__img {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.c-artice-item .item-left .item-left__img img {
  max-width: 100%;
}
.c-artice-item .item-left .item-left__text {
  position: absolute;
  right: 8px;
  bottom: 8px;
  width: 40px;
  height: 20px;
  line-height: 18px;
  text-align: center;
  border: 1px solid #232323;
  border-radius: 10px;
  color: #dcdcdc;
  font-size: 12px;
  background-color: #414141;
}
.c-artice-item .item-right {
  position: relative;
  width: 510px;
  padding-top: 10px;
}
.c-artice-item .item-right .item-right__titile {
  margin-bottom: 12px;
  color: #dcdcdc;
  font-size: 18px;
  font-weight: bolder;
}
.c-artice-item .item-right .item-right__titile:hover {
  color: #b4b4b4;
}
.c-artice-item .item-right .item-right__content {
  line-height: 1.3rem;
  margin-bottom: 8px;
  color: #a0a0a0;
  font-size: 14px;
}
.c-artice-item .item-right .item-right__tags {
  width: 100%;
  overflow: hidden;
}
.c-artice-item .item-right .item-right__tags a {
  float: left;
  margin-right: 10px;
  margin-bottom: 10px;
  font-size: 12px;
  color: #828282;
}
.c-artice-item .item-right .item-right__tags a.z-active,
.c-artice-item .item-right .item-right__tags a:hover {
  color: #dcdcdc;
}
.c-artice-item .item-right .item-right__bottom {
  position: absolute;
  left: 0;
  bottom: 10px;
}
.c-artice-item .item-right .item-right__img {
  float: left;
  width: 20px;
  height: 20px;
  overflow: hidden;
  margin-right: 8px;
  border-radius: 50%;
}
.c-artice-item .item-right .item-right__name {
  float: left;
  height: 20px;
  line-height: 20px;
  margin-right: 20px;
  color: #828282;
  font-size: 12px;
}
.c-artice-item .item-right .item-right__name:hover {
  color: #dcdcdc;
}
.c-artice-item .item-right .item-right__btn {
  display: inline-block;
  height: 20px;
  line-height: 20px;
  margin-right: 10px;
  font-size: 12px;
  color: #a0a0a0;
  vertical-align: middle;
}
.c-artice-item .item-right .item-right__btn:hover {
  color: #dcdcdc;
}
.c-artice-item a {
  text-decoration: none;
}
.c-artice-item:hover {
  z-index: 2;
  -webkit-transform: translate(0, -2px) translateZ(1px);
          transform: translate(0, -2px) translateZ(1px);
  box-shadow: 0 0 30px #323232;
}
.c-artice-list.z-small .item-left .top-img {
  background-color: #3c3c3c;
}
@-webkit-keyframes loading {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}
@keyframes loading {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}



