
.c-menu-left {
  position: absolute;
  top: 0;
  left: 0;
  width: 260px;
}
.c-menu-left .item {
  position: relative;
  width: 100%;
  height: 50px;
  overflow: hidden;
}
.c-menu-left .item a {
  display: block;
  width: 100%;
  height: 100%;
  padding-left: 20px;
}
.c-menu-left .item .item__icon {
  float: left;
  display: block;
  width: 20px;
  height: 50px;
  margin-right: 12px;
}
.c-menu-left .item .item__title {
  float: left;
  display: block;
  height: 50px;
  line-height: 50px;
  font-size: 14px;
  color: #dcdcdc;
  cursor: pointer;
  text-decoration: none;
}
.c-menu-left .item .item__title:hover {
  color: #dcdcdc;
}
.c-menu-left .item.z-on {
  background-color: #323232;
}
.c-menu-left .item:hover .item__title,
.c-menu-left .item.z-on .item__title {
  color: #dcdcdc;
}
.c-menu-left.z-left .item__title {
  padding-left: 40px;
  text-align: left;
}
.c-menu-left.z-tip .item__tip {
  display: block;
}



