
.c-bubble-mask {
  width: 100%;
  height: 100%;
  z-index: 20;
  position: fixed;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
}
.c-bubble-top {
  position: fixed;
  top: 20px;
  left: 50%;
  z-index: 30;
  width: 600px;
  height: 50px;
  line-height: 50px;
  margin-left: -300px;
  border-radius: 4px;
  font-size: 14px;
  color: #b4b4b4;
  text-align: center;
  box-shadow: 0 0 20px #323232;
}
.c-bubble-top.z-default {
  background-color: #00a08c;
}
.c-bubble-top.z-warn {
  background-color: #642d41;
}
.c-bubble-report {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 30;
  width: 300px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.c-bubble-report .report-title {
  width: 100%;
  height: 50px;
  line-height: 50px;
  padding-left: 30px;
  font-size: 14px;
  color: #828282;
  background-color: #2d2d2d;
}
.c-bubble-report .report-content {
  padding: 20px 30px 30px 30px;
  background-color: #4b4b4b;
}
.c-bubble-report .report-content .select-item {
  height: 20px;
  margin-bottom: 15px;
  cursor: pointer;
}
.c-bubble-report .report-content .select-item .select-item-radio {
  display: block;
  float: left;
  width: 20px;
  height: 20px;
  padding: 2px;
  margin-right: 10px;
  border: 2px solid #919191;
  border-radius: 50%;
}
.c-bubble-report .report-content .select-item .select-item-radio em {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.c-bubble-report .report-content .select-item .select-item-text {
  display: block;
  float: left;
  height: 20px;
  line-height: 20px;
  font-size: 14px;
