
.c-author-list {
  width: 100%;
  height: 100%;
}
.c-author-list .c-author-list-wrap {
  margin-bottom: 30px;
  background-color: #4b4b4b;
}
.c-author-list a {
  text-decoration: none;
}
.c-author-list .list-author__item {
  position: relative;
  display: table;
  width: 100%;
  height: 70px;
  padding: 0 20px;
  border-bottom: 1px solid #191919;
}
.c-author-list .list-author__item:hover {
  box-shadow: 0 0 30px #323232;
}
.c-author-list .list-author__item a {
  text-decoration: none;
}
.c-author-list .list-author__img {
  display: table-cell;
  vertical-align: middle;
  width: 30px;
}
.c-author-list .list-author__avatar {
  width: 30px;
  height: 30px;
  overflow: hidden;
  border-radius: 50%;
  background-color: #373737;
}
.c-author-list .list-author__content {
  display: table-cell;
  vertical-align: middle;
  width: 610px;
  padding-left: 10px;
}
.c-author-list .list-author__name {
  display: block;
  margin-bottom: 6px;
  color: #a0a0a0;
  font-size: 12px;
}
.c-author-list .list-author__text {
  overflow: hidden;
  font-size: 12px;
  color: #828282;
}
.c-author-list .list-author__work {
  float: left;
  margin-right: 10px;
}
.c-author-list .list-author__fans {
  float: left;
}
.c-author-list .list-author__btn {
  position: absolute;
  top: 20px;
  right: 20px;
  height: 30px;
  line-height: 30px;
  border-radius: 4px;
  background-color: #414141;
  font-size: 14px;
  color: #828282;
  cursor: pointer;
  text-align: center;
}
.c-author-list .list-author__btn.z-active,
.c-author-list .list-author__btn:hover {
  color: #a0a0a0;
  background-color: #373737;
}
.c-author-list .list-author__btn-1 {
  width: 50px;
}
.c-author-list .list-author__btn-2 {
  width: 60px;
}
.c-author-list .list-author__btn-3 {
  width: 80px;
}
.c-author-list.z-active .list-author__btn {
  float: right;
}
.c-author-item:last-child .c-author-item-wrap {
  border: none;
}



