.wangEditor-container {
  position: relative;
  z-index: 1;
  width: 100%;
  background-color: #4b4b4b;
}
.wangEditor-container .clearfix:after {
  content: '';
  display: table;
  clear: both;
}
.wangEditor-container .clearfix {
  *zoom: 1;
}
.wangEditor-container .height-tip {
  position: absolute;
  width: 3px;
  background-color: #ccc;
  left: 0;
  transition: top .2s;
}
.wangEditor-container .txt-toolbar {
  position: absolute;
  background-color: #fff;
  padding: 3px 5px;
  border-top: 2px solid #666;
  box-shadow: 1px 3px 3px #999;
  border-left: 1px\9 solid\9 #ccc\9;
  border-bottom: 1px\9 solid\9 #999\9;
  border-right: 1px\9 solid\9 #999\9;
}
.wangEditor-container .txt-toolbar .tip-triangle {
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  border: 5px solid;
  border-color: transparent transparent #666 transparent;
  top: -12px;
  left: 50%;
  margin-left: -5px;
}
.wangEditor-container .txt-toolbar a {
  color: #666;
  display: inline-block;
  margin: 0 3px;
  padding: 5px;
  text-decoration: none;
  border-radius: 3px;
}
.wangEditor-container .txt-toolbar a:hover {
  background-color: #f1f1f1;
}
.wangEditor-container .wangEditor-txt {
  width: 100%;
  padding: 40px;
  text-align: left;
}
.wangEditor-container .wangEditor-txt p {
  line-height: 1.5em;
  word-wrap: break-word;
  word-break: break-all;
  text-align: justify;
  color: #a0a0a0;
  font-size: 16px;
}
.wangEditor-container .wangEditor-txt blockquote {
  display: block;
  border-left: 8px solid #d0e5f2;
  padding: 5px 10px;
  margin: 10px 0;
  line-height: 1.4;
  font-size: 100%;
  background-color: #f1f1f1;
}
.wangEditor-container .wangEditor-txt img {
  width: auto;
  max-width: 100%;
  margin-bottom: 10px;
}
.wangEditor-container textarea {
  width: 100%;
  padding: 30px;
  margin-top: 0!important;
  height: auto!important;
  line-height: 1.5em;
  color: #a0a0a0;
  font-size: 16px;
}
.wangEditor-menu-container {
  width: 100%;
  height: 50px;
  background-color: #2d2d2d;
}
.wangEditor-menu-container .menu-group {
  float: left;
}
.wangEditor-menu-container .menu-item {
  float: left;
  position: relative;
  text-align: center;
  width: 40px;
  height: 50px;
  line-height: 50px;
}
.wangEditor-menu-container .menu-item:hover {
  background-color: #323232;
}
.wangEditor-menu-container .menu-item a {
  display: block;
  width: 100%;
  height: 100%;
  font-size: 14px;
  color: #a0a0a0;
  text-decoration: none;
}
.wangEditor-menu-container .menu-item a:hover {
  color: #dcdcdc;
}
.wangEditor-menu-container .menu-item a i {
  display: block;
  width: 100%;
  height: 100%;
}
.wangEditor-menu-container .menu-item.selected {
  color: #1e88e5;
}
.wangEditor-menu-container .menu-item.active {
  background-color: #f1f1f1;
}
.wangEditor-menu-container .menu-item.disable {
  opacity: 0.5;
  filter: alpha(opacity=50);
}
.wangEditor-menu-container .menu-item .wangeditor-menu-img-indent-left {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit-3.png');
}
.wangEditor-menu-container .menu-item .wangeditor-menu-img-indent-left:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit-3_on.png');
}
.wangEditor-menu-container .menu-item .wangeditor-menu-img-bold {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit-2.png');
}
.wangEditor-menu-container .menu-item .wangeditor-menu-img-bold:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit-2_on.png');
}
.wangEditor-menu-container .menu-item .wangeditor-menu-img-italic {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit-4.png');
}
.wangEditor-menu-container .menu-item .wangeditor-menu-img-italic:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit-4_on.png');
}
.wangEditor-menu-container .menu-item .wangeditor-menu-img-strikethrough {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit-5.png');
}
.wangEditor-menu-container .menu-item .wangeditor-menu-img-strikethrough:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit-5_on.png');
}
.wangEditor-menu-container .menu-item .wangeditor-menu-img-picture {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit-6.png');
}
.wangEditor-menu-container .menu-item .wangeditor-menu-img-picture:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit-6_on.png');
}
.wangEditor-menu-container .menu-item.menu-item-scan {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit-7.png');
}
.wangEditor-menu-container .menu-item.menu-item-scan:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit-7_on.png');
}
.wangEditor-menu-container .menu-item.j-save-btn-update {
  width: 80px;
}
.wangEditor-menu-container .menu-item.j-save-btn-update:hover {
  background-color: #2d2d2d;
}
.wangEditor-menu-container .menu-item.j-save-btn-update:hover a {
  cursor: default;
  color: #a0a0a0;
}
.wangEditor-menu-container .menu-tip {
  display: none;
  box-sizing: content-box;
  position: absolute;
  top: -36px;
  left: 50%;
  height: 36px;
  width: 48px;
  height: 30px;
  line-height: 30px;
  padding: 0 10px;
  margin-left: 0!important;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.8);
  color: #a0a0a0;
  font-size: 14px;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
.wangEditor-menu-container .menu-tip .tip-triangle {
  position: absolute;
  top: 25px;
  left: 50%;
  display: inline-block;
  border: 4px solid;
  border-color: transparent rgba(0, 0, 0, 0.8) rgba(0, 0, 0, 0.8) transparent;
  -webkit-transform: translate(-50%, 0) rotate(45deg);
          transform: translate(-50%, 0) rotate(45deg);
}
.wangEditor-menu-container .menu-tip-40 {
  width: 40px;
  margin-left: -20px;
}
.wangEditor-menu-container .menu-tip-50 {
  width: 50px;
  margin-left: -25px;
}
.c-edit {
  min-height: 700px;
}
.c-edit .c-edit-content {
  min-height: 650px;
  overflow-y: auto;
}



