
.c-feedback {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.c-feedback .c-feedback-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 360px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.c-feedback .content-form {
  width: 360px;
  padding: 30px;
  background-color: #4b4b4b;
}
.c-feedback .content-form .content-textarea {
  position: relative;
  width: 300px;
  height: 200px;
  padding: 20px;
  margin-bottom: 15px;
  background-color: #3c3c3c;
}
.c-feedback .content-form .content-textarea textarea {
  width: 100%;
  color: #5a5a5a;
  font-size: 14px;
}
.c-feedback .content-form .content-textarea .c-dialog__tip {
  -webkit-transform: translate(-114%, 0);
          transform: translate(-114%, 0);
}
.c-feedback .content-form .content-input {
  position: relative;
  width: 300px;
  height: 50px;
  line-height: 50px;
  padding-left: 20px;
  background-color: #3c3c3c;
}
.c-feedback .content-form .content-input input {
  width: 100%;
  color: #5a5a5a;
  font-size: 14px;
}
.c-feedback .content-form .content-input .c-dialog__tip {
  -webkit-transform: translate(-110%, 0);
          transform: translate(-110%, 0);
}
.c-feedback .content-btn {
  width: 100%;
  height: 50px;
  padding: 10px 0;
  overflow: hidden;
  background-color: #2d2d2d;
}
.c-feedback .content-btn .content-btn-item {
  float: left;
  width: 50%;
  height: 30px;
  line-height: 30px;
  text-align: center;
  color: #a0a0a0;
  font-size: 14px;
  cursor: pointer;
}
.c-feedback .content-btn .content-btn-item:hover {
  color: #dcdcdc;
}
.c-feedback .content-btn .content-btn-cancel {
  border-right: 1px solid #4b4b4b;
}



