
.c-footer {
  width: 100%;
  height: 100px;
  background-color: #323232;
}
.c-footer-content {
  height: 100%;
  margin: 0 auto;
  width: 960px;
}
.c-footer-content .footer-left {
  float: left;
  padding-top: 30px;
  font-size: 14px;
  color: #828282;
}
.c-footer-content .footer-left .menu {
  margin-bottom: 10px;
}
.c-footer-content .footer-left a {
  text-decoration: none;
  color: #828282;
}
.c-footer-content .footer-left a:hover {
  color: #dcdcdc;
}
.c-footer-content .footer-right {
  float: right;
  margin-top: 35px;
}
.c-footer-content .footer-right .icon-item {
  position: relative;
  display: block;
  float: left;
  width: 30px;
  height: 30px;
  margin-left: 20px;
  border-radius: 50%;
  background-color: #4b4b4b;
  cursor: pointer;
}
.c-footer-content .footer-right .icon-item .qr {
  display: none;
  position: absolute;
  top: -175px;
  left: 50%;
  z-index: 300;
  width: 140px;
  height: 140px;
  padding: 20px;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  box-shadow: 0 0 30px #323232;
  background-color: #4b4b4b;
  border-radius: 4px;
}
.c-footer-content .footer-right .icon-item .qr img {
  width: 100%;
  height: 100%;
}
.c-footer-content .footer-right .icon-item:nth-child(1) {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/ios01.png');
}
.c-footer-content .footer-right .icon-item:nth-child(1):hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/ios02.png');
}
.c-footer-content .footer-right .icon-item:nth-child(1):hover .qr {
  display: block;
}
.c-footer-content .footer-right .icon-item:nth-child(2) {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/android01.png');
}
.c-footer-content .footer-right .icon-item:nth-child(2):hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/android02.png');
}



