
.c-side-menu {
  position: fixed;
  bottom: 160px;
  right: 60px;
  z-index: 100;
  width: 60px;
  height: 60px;
  transition: all 0.25s;
  border-radius: 50%;
  background-color: #323232;
  cursor: pointer;
}
.c-side-menu span {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  transition: opacity 0.25s;
}
.c-side-menu span:nth-child(1) {
  opacity: 1;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/top-1.png');
}
.c-side-menu span:nth-child(2) {
  opacity: 0;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/top-1_on.png');
}
.c-side-menu .c-dialog__tip {
  top: 50%;
  display: none;
  -webkit-transform: translate(-120%, -50%);
          transform: translate(-120%, -50%);
}
.c-side-menu:hover {
  box-shadow: 0 0 30px #323232;
}
.c-side-menu:hover span:nth-child(1) {
  opacity: 0;
}
.c-side-menu:hover span:nth-child(2) {
  opacity: 1;
}
.c-side-menu:hover .c-dialog__tip {
  display: block;
}



