
.c-chapter-list {
  position: relative;
}
.c-chapter-list .c-chapter-list-wrap {
  width: 100%;
  height: 100%;
  margin-bottom: 30px;
  background-color: #4b4b4b;
}
.c-chapter-list-item {
  position: relative;
  height: 80px;
  padding: 0 30px;
  border-bottom: 1px solid #191919;
}
.c-chapter-list-item .link {
  display: block;
  width: 100%;
  height: 100%;
}
.c-chapter-list-item a {
  text-decoration: none;
}
.c-chapter-list__top {
  position: relative;
  overflow: hidden;
  padding-top: 15px;
  margin-bottom: 15px;
}
.c-chapter-list__top .top-number {
  float: left;
  margin-right: 20px;
  color: #a0a0a0;
  font-size: 18px;
  font-weight: bolder;
}
.c-chapter-list__top .top-title {
  float: left;
  margin-right: 5px;
  color: #dcdcdc;
  font-size: 18px;
  font-weight: bolder;
}
.c-chapter-list__top .top-new {
  float: left;
  margin-top: 5px;
  color: #00beff;
  font-size: 16px;
}
.c-chapter-list__bottom {
  color: #828282;
  font-size: 12px;
}
.c-chapter-list__bottom .bottom-time {
  float: left;
  height: 12px;
  line-height: 12px;
  margin-right: 12px;
}
.c-chapter-list__bottom .bottom-follow {
  float: right;
}
.c-chapter-list__bottom .bottom-follow em {
  width: 12px;
  height: 12px;
  margin-right: 6px;
  display: inline-block;
  vertical-align: middle;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/read-3.png');
}
.c-chapter-list__bottom .bottom-follow em:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/read-3_on.png');
}
.c-chapter-list__bottom .bottom-follow span {
  display: inline-block;
  vertical-align: middle;
}
.c-chapter-list__bottom .bottom-comment {
  float: right;
  margin-right: 12px;
}
.c-chapter-list__bottom .bottom-comment em {
  width: 12px;
  height: 12px;
  margin-right: 6px;
  display: inline-block;
  vertical-align: middle;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/read-2.png');
}
.c-chapter-list__bottom .bottom-comment em:hover {
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/read-2_on.png');
}
.c-chapter-list__bottom .bottom-comment span {
  display: inline-block;
  vertical-align: middle;
}
.c-chapter-list__bottom .bottom-manger-item {
  float: left;
  height: 12px;
  line-height: 12px;
  margin-right: 12px;
  color: #a0a0a0;
  font-size: 12px;
  cursor: pointer;
}
.c-chapter-list__bottom .bottom-manger-item:hover {
  color: #dcdcdc;
}
.c-chapter-list__history {
  position: absolute;
  top: 50px;
  right: 0;
  width: 30px;
  height: 30px;
  background-color: #3c3c3c;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/history.png');
}
.c-chapter-list__history a {
  display: block;
  width: 100%;
  height: 100%;
}



